    </div>

    <div class="form_input_box"  style="position: relative">
      <label for="nom_password">Mot de passe:</label>
      <input minlength="8" :type="showPassword ? 'text' : 'password'" class="form_input" placeholder="" name="nom_password" v-model="inputPasswordText" required/>
    </div>

    <div class="form_input_box" style="position: relative">
      <label for="nom_repeatPassword">Confirmer le mot de passe:</label>
      <input minlength="8" :type="showPasswordRepeatPassword ? 'text' : 'password'" class="form_input" placeholder="" name="nom_repeatPassword" v-model="inputRepeatPassword"  required  />
    </div>

    <div class="form_input_box">
      <label for="nom_email">Email:</label>
      <input class="form_input" placeholder="" name="nom_email" type="email" v-model="inputEmailText" required  />
    </div>

    <div class="form_btnContainer">
      <button :class="checked ? 'form_btn' : 'form_btnNonActive'" :disabled="checked">Sign Up</button>

      <div class="checkbox-wrapper-13">
        <input id="c1-4" type="checkbox" v-model="checked">
        <label for="c1-4">J'ai lu et j'accepte les <a href="#" style="text-decoration: underline; color: black">conditions générales</a></label>
      </div>
    </div>

  </form>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      inputPasswordText: "",
      inputNameText: "",
      inputRepeatPassword: "",
      showPassword: false,
      showPasswordRepeatPassword: false,
      inputEmailText: "",
      checked: false,
      modal: false,

    };
  },
  methods: {
    submitForm(e) {

      e.preventDefault();

      const formData = {
        nom_email: this.inputEmailText.valueOf(),
        nom_name: this.inputNameText.valueOf(),
        nom_lastName: this.inputNameText.valueOf(),
        nom_password: this.inputPasswordText.valueOf(),
      };

      axios({
        method: 'post',
        url: 'http://149.102.144.124/?registration',
        data: {
          nom_email: JSON.stringify(this.inputEmailText),
          nom_name: JSON.stringify(this.inputNameText),
          nom_lastName:  JSON.stringify(this.inputNameText),
          nom_password: JSON.stringify(this.inputPasswordText),
        }
      }).then(function (response) {
        console.log(response)
        console.log(JSON.stringify(formData))
        // Обработайте успешный ответ здесь
        console.log('Успешный ответ:', response.data);
      })
          .catch(function (error) {
            // Обработайте ошибку здесь
            console.error('Ошибка:', error);
          })

    }
  }
}
</script>

